// app/components/Navbar.tsx
"use client";

import { signOut } from "next-auth/react";
import Link from "next/link";
import { useState } from "react";

export default function Navbar() {
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);

  const toggleUserMenu = () => {
    setIsUserMenuOpen(!isUserMenuOpen);
  };

  return (
    <nav className="bg-transparent fixed top-0 left-0 right-0 p-4 backdrop-blur-md z-50">
      <div className="container mx-auto flex justify-between items-center">
        <div className="flex items-center space-x-4">
          <Link href="/" className="text-xl font-bold text-blue-600">
            AI4CKD
          </Link>
          <Link href="/patients" className="text-lg text-gray-800 hover:text-blue-600">
            Patients
          </Link>
          <Link href="/dashboard" className="text-lg text-gray-800 hover:text-blue-600">
            Tableau de Bord
          </Link>
        </div>
        
        {/* Menu utilisateur */}
        <div className="relative">
          <button 
            onClick={toggleUserMenu} 
            className="text-lg text-gray-800 hover:text-blue-600"
          >
            Utilisateur
          </button>

          {/* Menu déroulant pour l'utilisateur */}
          {isUserMenuOpen && (
            <div className="absolute right-0 mt-2 bg-white shadow-lg rounded-md w-48">
              <button 
                onClick={() => signOut({ callbackUrl: "/" })} 
                className="block w-full text-left px-4 py-2 text-red-600 hover:bg-gray-100 rounded-md"
              >
                Se déconnecter
              </button>
            </div>
          )}
        </div>
      </div>
    </nav>
  );
}
